.card {
  width: 100px;
  height: 150px;
  text-align: center;
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 10px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
  font-size: 24px;
  animation: 1s ease-in-out forwards slide-in-from-bottom;
  display: flex;
  position: absolute;
  box-shadow: 5px 5px 10px #0003;
}

.top-card {
  width: 100%;
  justify-content: start;
  margin: 10px;
  display: flex;
}

.bot-card {
  width: 100%;
  justify-content: start;
  margin: 10px;
  display: flex;
  transform: rotateZ(180deg);
}

.red {
  color: red;
}

@keyframes slide-in-from-bottom {
  0% {
    opacity: 0;
    transform: translateY(100%);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

